* {
  margin: 0;
  padding: 0;
}

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  /* kemur í veg fyrir lárétt hopp þegar síða síkkar og fær scrollbar */
  overflow-y: scroll;

  font-size: 16px;
  font-family: 'Noto sans', sans-serif;
}

h1,
h2,
h3 {
  font-family: 'Noto serif', serif;
}

a {
  text-decoration: underline;
  color: #000;
}


